// 初始化
$(function() {
  initSubjectActive()
  initCourseShow()
  initVideoHover()
  onSubjectActive()
  onVideoHover()
})

// 变量
var activeSubject = location.hash || $($('.study__subject-item')[0]).attr('href') || $($('.study__course')[0]).data('id')

// 初始化专题active状态
function initSubjectActive() {
  resetSubjectActive()
  $('.study__subject-item').each(function() {
    if($(this).attr('href') === activeSubject) {
      $(this).addClass('active')
    }
  })
}

// 重置专题active状态
function resetSubjectActive() {
  $('.study__subject-item').removeClass('active')
}

// 初始化课程展示状态
function initCourseShow() {
  resetCourseShow()
  $(changeIdSelectorToClassSelector(activeSubject)).show()
}

// 重置课程展示状态
function resetCourseShow() {
  $('.study__course').hide()
}

// 初始化视频hover状态
function initVideoHover() {
  resetVideoHover()
  $('.study__course-item').each(function() {
    $($(this).find('.study__video-item')[0]).addClass('hover')
  })
}

// 重置视频hover状态
function resetVideoHover(parent) {
  if(parent) {
    parent.find('.study__video-item').removeClass('hover')
  } else {
    $('.study__video-item').removeClass('hover')
  }
}

// 监听专题active状态
function onSubjectActive() {
  $('.study__subject-item').click(function() {
    resetSubjectActive()
    $(this).addClass('active')
    resetCourseShow()
    $(changeIdSelectorToClassSelector($(this).attr('href'))).show()
  })
}

// 监听视频hover状态
function onVideoHover() {
  $('.study__video-item').on('mouseover click', function() {
    resetVideoHover($(this).parents('.study__course-item'))
    $(this).addClass('hover')
  })
}
